import {Table} from 'antd';
import dataSource from './DataSource';
import './Styles.css';

const columns = [
  {
    title:'Sno.',
    dataIndex:'sno',
    key:'sno',
  },
  {
    title: 'Name',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Age',
    dataIndex: 'age',
    key: 'age',
    sorter: (a, b) => a.age - b.age,
  },
  {
    title: 'Address',
    dataIndex: 'address',
    key: 'address',
    filters:[{
      text:'10 Downing Street',
      value:'10 Downing Street',
    },
    {
      text:'New York',
      value:'New York',
    }],
     onFilter: (value, record) => record.address.indexOf(value) === 0,
    /* */
  },
  {
      title:'Skills',
      dataIndex:'tags',
      key:'tags',

  },

 
];

function App(){ 
    return(
        <div className="container table-responsive ">
       
            <Table className="table table-striped table-bordered table-danger table-hover" dataSource={dataSource} columns={columns} pagination={{ pageSize: 10 }} scroll={{ y: 300 }} title={() => <h4 style={{backgroundColor:'pink',padding:40,fontSize:50,textAlign:'center'}}>Student Details</h4>}/>
            
           
        </div>

    )
}

export default App;

